import BScroll from "better-scroll";
import { getSheetDetail } from "api/api_songsheet.js";
import List from "base/List";
import Skeleton from "base/Skeleton";
// import MHeader from 'base/MHeader'
import { mapMutations, mapActions } from "vuex";
 import scrollMixin from "@/mixin/scrollMixin"
export default {
  mixins:[scrollMixin],
  components: {
    List,
    Skeleton
    // MHeader
  },
  name: "SongSheet",
  data() {
    return {
      title: "歌单",
      info: {
        creator: {}
      },
      songList: []
    };
  },
  created() {
    this.transformDelta=49
  },
  mounted() {
    window.addEventListener("touchmove", this.scorll);
  },
  activated() {
    let id = this.$route.params.id;
    this.getDetail(id);
  },
  computed: {
    // title() {
    //   this.$nextTick(() => {
    //     debugger
    //     if (this.$refs.main.scrollTop < 50) return "歌单";
    //   });
    // }
  },
  watch: {},
  methods: {
    selectItem(index, item) {
      // debugger
      this.selectSong({ list: this.songList, song: item, index });
    },
    scorll() {
      if (this.$refs.wrapper && this.$refs.wrapper.scrollTop > 50) {
        // debugger
        this.title = this.info.name;
      } else {
        this.title = "歌单";
      }
    },
    async getDetail(id) {
      let data = await getSheetDetail(id);
      console.log('result',data)

      //debugger
      // console.log(data);
      let info = {
        coverImgUrl: data.coverImgUrl,
        name: data.name,
        createTime: data.createTime,
        trackCount: data.trackCount,
        playCount: data.playCount,
        description: data.description,
        tags: data.tags,
        creator: {
          avatarUrl: data.creator.avatarUrl || "",

          nickname: data.creator.nickname
        }
      };
      this.info = info;
      //this.info.title="歌单";
      data.tracks.forEach(element => {
        //let d = (element.duration / 1000).toFixed() * 1;
        element.singer = element.ar.map(t => t.name).join("/"); //歌手
      });
      this.songList = data.tracks;
      console.log('data.tracks',data.tracks)

    },
    back() {
      this.$router.go(-1);
    },
    ...mapMutations({
      setPlayList: "SET_PLAY_LIST"
    }),
    ...mapActions({
      selectSong: "SELECT_SONG"
    })
  }
};



